import React, {Component} from 'react';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {actionCreator} from '../../../../actions';
import * as types from '../../../../constants/actiontypes';

const styles = {
    tagRadio: {
      marginLeft: 5,
      marginRight: 15,
    },
  };

class RadioButton extends Component {

  componentDidMount() {
  }

  constructor(props, context) {
    super(props, context);
    
  }

  change = (tagId, status) => {
    this.props.clickChange(tagId, status, this.props.actionType);
  }

  render() {

    return (
        <div>
          <label>&nbsp;<input type="radio" name={this.props.name} style={styles.tagRadio} 
            onChange={(e, target) => this.change(this.props.radio.tag_id, 1)} 
            checked={this.props.radio.tag_score} /></label>

          <label>&nbsp;<input type="radio" name={this.props.name} style={styles.tagRadio} 
            onChange={(e, target) => this.change(this.props.radio.tag_id, 0)} 
            checked={!this.props.radio.tag_score} /></label>
        </div>
    );
  }
}
RadioButton.propTypes = {
    clickChange: PropTypes.func,
};

const mapStateToProps = state => ({
  
});

const mapDispatchToProps = dispatch => ({
    clickChange: (tagId, status, actionType) => {
      dispatch(actionCreator(actionType, [tagId, status]));
    },
});

module.exports = connect(mapStateToProps, mapDispatchToProps)(RadioButton);

export default RadioButton;



